<template>
  <div class="container">
    <div class="container-top">
      <el-form class="form-style" ref="form" :model="form">
        <el-form-item>
          <el-input  :autosize="{ minRows: 10, maxRows: 20}" type="textarea" v-model="form.desc"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit">提交留言</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: 'lcz-message-main',
  data () {
    return {
      form: {
        desc: ''
      }
    }
  },
  methods: {
    onSubmit () {
      this.$message({
        message: '提交成功',
        type: 'success'
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.container {
  min-height: 100vh;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  align-items: center;
  box-sizing: border-box;
  padding: 5rem 10rem;

  .form-style {
    width: 700px;
    min-width: 700px;
  }
}
</style>



